<template>
  <div class="pageContent">
    <el-tabs v-model="activeName">
      <el-tab-pane label="全部交易记录" name="0">
        <all :getPost="activeName == 0" @changeActive="changeActive"></all>
      </el-tab-pane>
      <el-tab-pane label="充值记录" name="1">
        <recharge :getPost="activeName == 1"></recharge>
      </el-tab-pane>
      <el-tab-pane label="提现记录" name="2">
        <cashout :getPost="activeName == 2"></cashout>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import all from './components/record/all';
import recharge from './components/record/recharge';
import cashout from './components/record/cashout';
import { mapState } from 'vuex'
export default {
  data(){
    return{
      activeName:0
    }
  },
  created(){
    if(this.$route.query.type) this.activeName = this.$route.query.type + ''
  },
  methods:{
    changeActive(e){
      console.log('父级执行！',e)
      this.activeName = e
      window.scroll(0,0)
    },
  },
  computed: {
    ...mapState([
      'shop',
      'user'
    ]),
  },
  components: {
    all,
    recharge,
    cashout
  },
}
</script>
<style lang="less" scoped>

</style>